import React, { useEffect } from 'react'
import styles from '../styles/pages/Company.module.css'
import Header from '../components/Header'

// kakao  
declare global {
  interface Window {
    kakao: any;
  }
}

export function Kakao() {
  
  useEffect(() => {
    const { kakao } = window;

    //    DOM
    const container = document.getElementById('map');
    //      
    const options = {
      //  
      center: new kakao.maps.LatLng(35.1524174833396, 129.059590929327),
      level: 3
    };

    //     
    const map = new kakao.maps.Map(container, options);

    //   
    const markerPosition = new kakao.maps.LatLng(35.1524174833396, 129.059590929327); 

    //  
    const marker = new kakao.maps.Marker({
      position: markerPosition
    });

    marker.setMap(map);

    const iwContent = '<div style="padding:8px; font-size:14px">  </div>', //    HTML  document element 
    iwPosition = new kakao.maps.LatLng(35.1524174833396, 129.059590929327); //  

    //  
    const infowindow = new kakao.maps.InfoWindow({
      position : iwPosition, 
      content : iwContent 
    })

    //    .   marker     
    infowindow.open(map, marker); 


  }, [])

  return (
    <div id="map" style={{width: '100%', height: '500px'}}></div>
  )
}


export default function Company() {
  return (

    <div  className='flex__container'>
      <Header />
      <div className={styles.company__title}> </div>
      <Kakao />
      <div className={styles.company__des}>
        <div>
          <p className={styles.company__des__title}></p>
          <p>   668,  4</p>
        </div>
        <div>
          <p className={styles.company__des__title}></p>
          <p> : 000-0000-0000</p>
          <p> : JJJ@DepartmentStore.com</p>
        </div>
      </div>

      <div className={styles.company__diraction}>
        <div>
          <p className={styles.company__diraction__title}>  </p>
          <p>
               (10 ) &rarr;   10
          </p>
        </div>
        <div>
          <p className={styles.company__diraction__title}>  </p>
          <p>
                (29, 43, 52, 62, 80, 86, 103, 108, 133, 1004, 1001, 1002) &rarr;  
          </p>
        </div>
        <div>
          <p className={styles.company__diraction__title}>  </p>
          <p>
                
          </p>
        </div>
      </div>
    </div>
  )
}