// 
//  
import React, { useEffect, useState } from 'react';
import styles from '../styles/pages/EditUser.module.css';
import Modal from 'react-modal';
import DaumPostcodeEmbed, { Address } from 'react-daum-postcode';
import CloseIcon from '@mui/icons-material/Close';
import {
  Button,
  FormControl,
  FormControlLabel,
  Radio,
  RadioGroup,
} from '@mui/material';
import { getUserById, updateUser } from '../services/userServices';
import { User } from '../types/type';

interface ErrorForm {
  userName: string;
  userPhone: string;
  userEmail: string;
  userAddress: string;
  userAddressDetail: string;
}

const initialError: ErrorForm = {
  userName: '',
  userEmail: '',
  userPhone: '',
  userAddress: '',
  userAddressDetail: '',
};

export default function EditUser() {
  // db.json    
  const [user, setUser] = useState<Partial<User>>({});
  const [errors, setErrors] = useState<ErrorForm>(initialError);
  const userId = 1;
  let {
    userLoginId,
    userName,
    userEmail,
    userPhone,
    userZipCode,
    userAddress,
    userAddressDetail,
    userGender,
    userBirth,
  } = user;
  useEffect(() => {
    const fetchUser = async () => {
      try {
        const fetchedUser = await getUserById(userId);
        console.log(fetchedUser);
        setUser(fetchedUser);
      } catch (error) {
        console.error('Failed to fetch user:', error);
      }
    };
    fetchUser();
  }, [userId]);

  const validateName = (name: string): boolean => {
    return /^[a-zA-Z-][a-zA-Z-\s]*[a-zA-Z-]$/.test(name);
  };

  const validateEmail = (email: string): boolean => {
    return /\S+@\S+\.\S+/.test(email);
  };

  const validatePhone = (phone: string): boolean => {
    return /^010\d{8}$/.test(phone);
  };

  const validationRules = {
    userName: (value: string) => validateName(value),
    userEmail: (value: string) => validateEmail(value),
    userPhone: (value: string) => validatePhone(value.replace(/-/g, '')),
    userAddress: (value: string) => !!value,
    userAddressDetail: (value: string) => !!value,
  };

  const errorMessages: Record<string, string> = {
    userName: '  ',
    userEmail: '  ',
    userPhone: '   ',
    userAddress: '  ',
    userAddressDetail: ' ',
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    let tempErrors: ErrorForm = { ...initialError };
    console.log(tempErrors);
    let isValid = true;

    Object.keys(validationRules).forEach((key) => {
      const fieldKey = key as keyof ErrorForm;
      const value = user[fieldKey] as string;
      const validate = validationRules[fieldKey];

      if (validate) {
        const isValidField = validate(value);
        if (!isValidField) {
          tempErrors[fieldKey] = errorMessages[fieldKey] || '';
          isValid = false;
        } else {
          tempErrors[fieldKey] = '';
        }
      }
    });
    setErrors(tempErrors);

    if (isValid) {
      try {
        await updateUser(userId, {
          ...user,
          userName,
          userEmail,
          userPhone,
          userAddress,
          userAddressDetail,
          userGender,
          userBirth,
        });
        console.log('  : ', user);
        alert(`  ! ${userName}! `);
        setUser(user);
      } catch (error) {
        console.log(error);
        alert('FAIL update user');
      }
    }
  };

  const handleInputChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>
  ) => {
    const { name, value } = e.target;
    setUser({ ...user, [name]: value });

    //     
    //  input 
    //      
    if (name in validationRules) {
      const key = name as keyof typeof validationRules;

      if (errors[key]) {
        const isValid = validationRules[key](value as string);
        setErrors((prev) => ({
          ...prev,
          [name]: isValid ? '' : errorMessages[name] || '',
        }));
      }
    }
  };

  // input focus   input 
  const handleBlur = (e: React.FocusEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    if (name in validationRules) {
      const key = name as keyof typeof validationRules;
      const isValid = validationRules[key](value as string);
      setErrors((prev) => ({
        ...prev,
        [name]: isValid ? '' : errorMessages[name] || '',
      }));
    }
  };

  //!   
  const today = new Date();
  const handleDateChange = (field: 'year' | 'month' | 'day', value: string) => {
    const year = userBirth?.slice(0, 4) || '';
    const month = userBirth?.slice(5, 7) || '';
    const day = userBirth?.slice(8, 10) || '';

    const newDate =
      field === 'year'
        ? `${value}-${month}-${day}`
        : field === 'month'
        ? `${year}-${value}-${day}`
        : `${year}-${month}-${value}`;

    setUser((prev) => ({
      ...prev,
      userBirth: newDate,
    }));
  };

  const selectedYear = userBirth?.slice(0, 4) || '';
  const selectedMonth = userBirth?.slice(5, 7) || '';
  const selectedDay = userBirth?.slice(8, 10) || '';

  const years: string[] = [''];
  for (let y = today.getFullYear(); y >= 1930; y--) {
    years.push(y.toString());
  }

  const months: string[] = [''];
  for (let m = 1; m <= 12; m++) {
    months.push(m.toString().padStart(2, '0'));
  }

  let date = new Date(Number(selectedYear), Number(selectedMonth), 0).getDate();
  let days: string[] = [''];
  for (let d = 1; d <= date; d++) {
    days.push(d.toString().padStart(2, '0'));
  }

  //!   
  const [isFindModalOpen, setIsFindModalOpen] = useState<boolean>(false);
  const handleSearchAddress = () => {
    setIsFindModalOpen(!isFindModalOpen);
  };
  const handleComplete = (data: Address) => {
    setUser({
      ...user,
      userZipCode: data.zonecode,
      userAddress: data.address,
    });
    setIsFindModalOpen(false);
  };

  // SignUp.tsx    
  //   
  const [password, setPassword] = useState('');
  const [isVerified, setIsVerified] = useState(false);

  const handlePasswordCheck = (e: React.ChangeEvent<HTMLInputElement>) => {
    setPassword(e.target.value);
  };

  const handleCheckSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    if (password === user.userPassword) {
      setIsVerified(true);
    } else {
      alert('  .    .');
    }
  };

  //   
  const handleCancel = () => {
    setIsVerified(false);
    setPassword('');
  };

  return (
    <div className={styles.edit__container}>
      <div className={styles.edit__header}></div>

      {isVerified ? (
        //   
        <div>
          <div className={styles.login__section}>
            <form className={styles.login__form}>
              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.input__box}>
                    <input
                      className={styles.input}
                      type='text'
                      name='userName'
                      maxLength={20}
                      placeholder=' -    '
                      value={userName}
                      onChange={handleInputChange}
                      onBlur={handleBlur}
                    />
                  </div>
                </div>
                {errors.userName ? (
                  <p className={styles.error}>{errors.userName}</p>
                ) : (
                  ' '
                )}
              </div>

              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.input__box}>
                    <input
                      type='text'
                      className={styles.input}
                      name='userEmail'
                      maxLength={50}
                      placeholder='example@gmail.com'
                      value={userEmail}
                      onChange={handleInputChange}
                      onBlur={handleBlur}
                    />
                  </div>
                </div>
                {errors.userEmail ? (
                  <p className={styles.error}>{errors.userEmail}</p>
                ) : (
                  ' '
                )}
              </div>

              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.input__box}>
                    <input
                      className={styles.input}
                      type='text'
                      name='userPhone'
                      minLength={11}
                      maxLength={11}
                      placeholder='ex) 01012345678 - (-)  '
                      value={userPhone?.replace(/-/g, '')}
                      onChange={handleInputChange}
                      onBlur={handleBlur}
                    />
                  </div>
                </div>
                {errors.userPhone ? (
                  <p className={styles.error}>{errors.userPhone}</p>
                ) : (
                  ' '
                )}
              </div>

              <div className={styles.input__container}>
                <div className={`${styles.input__wrapper} ${styles.btn__box}`}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.address__box}>
                    <div className={styles.input__box}>
                      <input
                        className={styles.input}
                        name='userZipCode'
                        value={userZipCode}
                        placeholder=''
                        readOnly
                        onChange={handleInputChange}
                        onBlur={handleBlur}
                      />
                    </div>
                    <Button
                      onClick={handleSearchAddress}
                      color='info'
                      sx={{
                        borderRadius: '10px',
                        cursor: 'pointer',
                        position: 'absolute',
                        right: '-70px',
                        top: '5px',
                      }}
                    >
                      
                    </Button>

                    <div className={styles.input__box}>
                      <input
                        className={styles.input}
                        name='userAddress'
                        value={userAddress}
                        placeholder=''
                        readOnly
                        onChange={handleInputChange}
                        onBlur={handleBlur}
                      />
                    </div>

                    <Modal
                      isOpen={isFindModalOpen}
                      style={modalStyles}
                      ariaHideApp={false}
                      onRequestClose={() => setIsFindModalOpen(false)}
                    >
                      <div
                        id='closeBtn'
                        style={{
                          display: 'flex',
                          justifyContent: 'end',
                          marginBottom: '10px',
                        }}
                      >
                        <CloseIcon
                          sx={{ cursor: 'pointer' }}
                          onClick={() => setIsFindModalOpen(false)}
                        />
                      </div>
                      <DaumPostcodeEmbed
                        onComplete={handleComplete}
                        style={{ height: '95%' }}
                      />
                    </Modal>

                    <div className={styles.input__box}>
                      <input
                        className={styles.input}
                        name='userAddressDetail'
                        maxLength={50}
                        value={userAddressDetail}
                        placeholder=''
                        onChange={handleInputChange}
                        onBlur={handleBlur}
                      />
                    </div>
                  </div>
                </div>
                <div className={styles.input__container}>
                  {!userAddress && errors.userAddress && (
                    <p className={styles.error}>{errors.userAddress}</p>
                  )}

                  {userAddress &&
                    !userAddressDetail &&
                    errors.userAddressDetail && (
                      <p className={styles.error}>{errors.userAddressDetail}</p>
                    )}
                </div>
              </div>

              <div className={styles.more__info__container}>
                <div className={styles.more__info}></div>
                <div className={styles.more__box}></div>
              </div>

              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <FormControl>
                    <RadioGroup
                      name='userGender'
                      value={userGender}
                      onChange={handleInputChange}
                      sx={{
                        width: '300px',
                        display: 'flex',
                        flexDirection: 'row',
                        marginLeft: '60px',
                      }}
                    >
                      <FormControlLabel
                        value='male'
                        control={<Radio />}
                        label=''
                      />

                      <FormControlLabel
                        value='female'
                        control={<Radio />}
                        label=''
                      />
                    </RadioGroup>
                  </FormControl>
                </div>
              </div>

              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.input__box}>
                    <select
                      className={styles.birth__select}
                      value={selectedYear}
                      onChange={(e) => {
                        handleDateChange('year', e.target.value);
                      }}
                    >
                      {years.map((item) => (
                        <option value={item} key={item}>
                          {item}
                        </option>
                      ))}
                    </select>

                    <select
                      className={styles.birth__select}
                      value={selectedMonth}
                      onChange={(e) =>
                        handleDateChange('month', e.target.value)
                      }
                      disabled={selectedYear === ''}
                    >
                      {months.map((item) => (
                        <option value={item} key={item}>
                          {item}
                        </option>
                      ))}
                    </select>

                    <select
                      className={styles.birth__select}
                      value={selectedDay}
                      onChange={(e) => handleDateChange('day', e.target.value)}
                      disabled={
                        selectedMonth === '' || selectedYear === ''
                      }
                    >
                      {days.map((item) => (
                        <option value={item} key={item}>
                          {item}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>
              </div>

              <div className={styles.button__container}>
                <Button
                  type='button'
                  onClick={handleCancel}
                  sx={{
                    width: '100%',
                    fontSize: 'var(--font-size-regular)',
                  }}
                >
                  
                </Button>
                <Button
                  type='submit'
                  onClick={(e) => {
                    handleSubmit(e);
                  }}
                  sx={{
                    width: '100%',
                    fontSize: 'var(--font-size-regular)',
                  }}
                >
                  
                </Button>
              </div>
            </form>
          </div>
        </div>
      ) : (
        //   
        <div>
          <div className={styles.edit__check__title}> </div>
          <div style={{ marginBottom: '30px' }}>
                   
          </div>
          <div className={styles.login__section}>
            <form className={styles.login__form} onSubmit={handleCheckSubmit}>
              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.input__box}>
                    <input
                      className={styles.input}
                      type='text'
                      name='userLoginId'
                      value={userLoginId}
                      disabled
                    />
                  </div>
                </div>
              </div>
              <div className={styles.input__container}>
                <div className={styles.input__wrapper}>
                  <div className={styles.label__box}>
                    <label></label>
                  </div>
                  <div className={styles.input__box}>
                    <input
                      className={styles.input}
                      type='text'
                      name='password'
                      placeholder='8 ~ 12   '
                      value={password}
                      onChange={handlePasswordCheck}
                    />
                  </div>
                </div>
              </div>
              <div className={styles.button__container}>
                <Button
                  type='button'
                  onClick={handleCancel}
                  sx={{
                    width: '100%',
                    fontSize: 'var(--font-size-regular)',
                  }}
                >
                  
                </Button>
                <Button
                  type='submit'
                  sx={{
                    width: '100%',
                    fontSize: 'var(--font-size-regular)',
                  }}
                >
                  
                </Button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}

const modalStyles: ReactModal.Styles = {
  overlay: {
    backgroundColor: 'rgba(0,0,0,0.5)',
  },

  content: {
    width: '500px',
    height: '600px',
    margin: 'auto',
    overflow: 'hidden',
    padding: '20px',
    position: 'absolute',
    borderRadius: '10px',
    boxShadow: '2px 2px 2px rgba(0, 0, 0, 0.25)',
    backgroundColor: 'white',
    justifyContent: 'center',
  },
};
